@use '../base/mixins' as *;
@use '../base/variables' as *;

.acciones {
    margin-top: 5rem;
    @include tablet {
        display: flex;
        justify-content: space-between;
    }
    
    a {
        display: block;
        text-align: center;
        margin-bottom: 2rem;
        color: $negro;
        &:last-of-type {
            margin: 0;
        }
    }
}